.howitworks {
    position: relative;
    z-index: 10;
    color: $main-color-dark;
    &__container {
        padding: 88px 100px; }
    &__title {
        margin-bottom: 50px;
        line-height: 1; }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        @include sm {
            flex-direction: column; } }
    &__column {
        flex-basis: 45%;
        display: flex;
        flex-direction: column; } }

.work {
    position: relative;
    &:not(:last-child) {
        margin-bottom: 55px; }
    &__icon {
        position: absolute;
        top: 9px;
        left: 0; }
    &__text {
        margin-left: 75px;
        font-size: 18px;
        font-weight: 300;
        line-height: 2; } }

