body {
	background: $main-color-light;
	// +css-lock(16, 20, 600, 2000)
	line-height: 1.4;
	font-family: $Montserrat, sans-serif; }

* {
	box-sizing: border-box; }

image {
	max-width: 100%;
	height: auto; }

a {
	text-decoration: none;
	color: inherit; }

.container-inner {
	width: 970px;
	margin: 0 auto;
	@include sm {
    width: 90%; } }
.white-rounded-container {
	width: 1170px;
	margin: 0 auto;
	background-color: #fff {
  	box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25); }
	border-radius: 16px;
	@include sm {
  	width: 90%; } }

// ===== BUTTONS =====
.btn {
	display: inline-block;
	border-radius: 32px;
	height: 64px;
	padding: 0 65px;
	margin: 0 auto;
	line-height: 64px;
	font-weight: 600;
	text-align: center; }
.btn-dark {
	background-color: $main-color-dark;
	color: #fff; }

.btn-light {
	background-color: #fff;
	color: $main-color-dark; }
