.forecast {
    position: relative;
    background-color: $main-color-dark;
    z-index: 10;
    padding: 144px 0 144px;
    color: #fff;
    // margin-top: -48px
    // overflow-y: hidden
    &__container {
        position: relative;
        z-index: 5; }
    &__descr {
        margin-bottom: 43px;
        font-size: 24px;
        font-weight: 600;
        line-height: 1.5; }
    &__buttons {
        display: flex;
        margin-bottom: 42px; }
    &__btn {
        display: block;
        width: 136px;
        height: 84px;
        border-radius: 8px;
        text-align: center;
        border: solid 8px #223e5c;
        line-height: 68px;
        font-size: 30px;
        font-weight: bold;
        vertical-align: top;
        &:not(:last-child) {
            margin-right: 30px; }
        &-active {
            background-color: $main-color-light;
            border: solid 8px $main-color-light; } }

    &__image {
        position: relative;
        z-index: 5;
        img {
            width: 100%; } }
    &:before {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 200%;
        background-color: $main-color-dark;
        z-index: 10;
        transform: skewY(30deg);
        transform-origin: top right;
        z-index: 2; }
    &:after {
        position: absolute;
        content: '';
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 200%;
        background-color: $main-color-dark;
        z-index: 10;
        transform: skewY(30deg);
        transform-origin: bottom left;
        z-index: 2; }
    .forecast__cube-top {
        position: absolute;
        top: calc(-1 * (56vw + 149px));
        left: -20vw;
        z-index: 10; } }
